// Payments.js
import React, { useState } from 'react';
import { Button, Container, Form, Alert, Spinner } from 'react-bootstrap';
import { ethers, BrowserProvider } from 'ethers';
import { Link } from 'react-router-dom';

const Payments = () => {
  const [walletAddress, setWalletAddress] = useState(null);
  const [amount, setAmount] = useState('0.01'); // Default payment amount in ETH
  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  // Connect to MetaMask
  const connectWallet = async () => {
    if (window.ethereum) {
      try {
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        setWalletAddress(accounts[0]);
      } catch (error) {
        setError("Connection failed. Please try again.");
      }
    } else {
      setError("MetaMask not found. Please install it.");
    }
  };

  // Handle Payment
  const handlePayment = async () => {
    if (!walletAddress) {
      setError("Please connect your wallet first.");
      return;
    }

    setIsProcessing(true);
    setError(null);
    setSuccess(null);

    try {
      const provider = new BrowserProvider(window.ethereum);
      const signer = provider.getSigner();

      // Define transaction parameters
      const tx = {
        to: '0xb6db403a33f45257c06aae378355779d1fc6b61a', // Replace with your receiving wallet address
        value: ethers.parseEther(amount),
      };

      // Send transaction
      const transactionResponse = await signer.sendTransaction(tx);
      await transactionResponse.wait(); // Wait for transaction to be mined

      setSuccess("Payment successful! Thank you.");
    } catch (error) {
      setSuccess("Payment failed. Please try again.");
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <Container style={{ maxWidth: '400px', marginTop: '50px' }}>
      <h3>Complete Your Payment</h3>

      {error && <Alert variant="danger">{error}</Alert>}
      {success && <Alert variant="success">{success}</Alert>}

      <Form>
        <Form.Group controlId="formWallet">
          <Form.Label>Wallet Address</Form.Label>
          <Form.Control
            type="text"
            placeholder="Not connected"
            value={walletAddress || ''}
            readOnly
          />
        </Form.Group>

        <Form.Group controlId="formAmount">
          <Form.Label>Amount (ETH)</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter amount in ETH"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
          />
        </Form.Group>

        {!walletAddress ? (
          <Button variant="primary" onClick={connectWallet} className="mt-3">
            Connect Wallet
          </Button>
        ) : (
          <Button
            variant="success"
            onClick={handlePayment}
            disabled={isProcessing}
            className="mt-3"
          >
            {isProcessing ? <Spinner animation="border" size="sm" /> : "Pay with MetaMask"}
          </Button>
        )}
      </Form>
    </Container>
  );
};

export default Payments;
