import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Style/TaskForm.css';

const TaskForm = () => {
    const [status, setStatus] = useState('Pending');
    const [walletConnected, setWalletConnected] = useState(false);

    const handleStatusChange = (e) => setStatus(e.target.value);
    const handleWalletConnect = () => setWalletConnected(!walletConnected);

    return (
        <div className="task-form-container">
            <h1 className="form-title">Quest Task Submission</h1>

            {/* Task Details Section */}
            <section className="task-section">
                <h2>Task Details</h2>
                <label>
                    Task Title
                    <input type="text" placeholder="Enter task name, e.g., 'Find Two Diamonds'" />
                </label>
                <label>
                    Task Description
                    <textarea placeholder="Provide a description of the quest..." />
                </label>
                <label>
                    Reward Offered
                    <input type="text" placeholder="e.g., Custom NFT" />
                </label>
                <label>
                    XPin Tracking ID
                    <input type="text" placeholder="Enter XPin ID for tracking" />
                </label>
                <label>
                    Completion Status
                    <select value={status} onChange={handleStatusChange}>
                        <option value="Pending">Pending</option>
                        <option value="Completed">Completed</option>
                        <option value="Incomplete">Incomplete</option>
                        <option value="Expired">Expired</option>
                    </select>
                </label>
                <button 
                    className={`connect-wallet-btn ${walletConnected ? 'connected' : ''}`} 
                    onClick={handleWalletConnect}>
                    {walletConnected ? 'Wallet Connected' : 'Connect Wallet to Claim Rewards'}
                </button>
            </section>

            {/* Plugin Setup Section */}
            <section className="plugin-section">
                <h2>Plugin Configuration</h2>
                <label>
                    World or Adventure Name
                    <input type="text" placeholder="Name of the in-game world, e.g., 'Adventure Zone'" />
                </label>
                <label>
                    Setup Guidelines
                    <textarea placeholder="Add specific guidelines for task setup." />
                </label>
                <label>
                    Required Clicks for Verification
                    <input type="text" placeholder="Enter clicks required for validation" />
                </label>
                <label>
                    Max Players Allowed
                    <select>
                        <option value="10">10</option>
                        <option value="20">20</option>
                        <option value="50">50</option>
                    </select>
                </label>
            </section>

            {/* Task Theme Section */}
            <section className="theme-section">
                <h2>Customize Task Theme</h2>
                <label>
                    Quest Theme
                    <input type="text" placeholder="e.g., 'Collecting Diamonds'" />
                </label>
                <label>
                    Quest Type
                    <select>
                        <option value="Treasure Hunt">Treasure Hunt</option>
                        <option value="Map Explorer">Map Explorer</option>
                        <option value="Ore Collection">Ore Collection</option>
                    </select>
                </label>
                <Link to='/results' className="publish-btn">Publish Task</Link>
            </section>
        </div>
    );
};

export default TaskForm;
