import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const MinecraftTaskGenerator = () => {
  const [taskDescription, setTaskDescription] = useState('');
  const [duration, setDuration] = useState(1);
  const [rewards, setRewards] = useState('');
  const [price, setPrice] = useState(0);
  const [generatedTask, setGeneratedTask] = useState(null);

  // Calculate price based on duration and rewards
  const calculatePrice = () => {
    const basePrice = 10; // Base price per unit of duration
    const rewardFactor = 5; // Price factor for adding rewards
    return basePrice * duration + rewardFactor * rewards.length;
  };

  // Handle form submission
  const handleSubmit = (event) => {
    event.preventDefault();
    const calculatedPrice = calculatePrice();
    setPrice(calculatedPrice);

    // Generate the task details
    const task = {
      description: taskDescription,
      duration,
      rewards,
      price: calculatedPrice,
    };
    setGeneratedTask(task);
  };

  return (
    <div className="container text-center" style={{ padding: '50px' }}>
      <h2>Minecraft Task Query Generator</h2>
      
      {/* Task Creation Form */}
      <form onSubmit={handleSubmit} style={{ maxWidth: '400px', margin: 'auto' }}>
        <div className="mb-3">
          <label htmlFor="taskDescription" className="form-label">Task Description</label>
          <input
            type="text"
            className="form-control"
            id="taskDescription"
            value={taskDescription}
            onChange={(e) => setTaskDescription(e.target.value)}
            required
          />
        </div>
        <div className="mb-3">
          <label htmlFor="duration" className="form-label">Duration (hours)</label>
          <input
            type="number"
            className="form-control"
            id="duration"
            value={duration}
            onChange={(e) => setDuration(parseInt(e.target.value))}
            min="1"
            required
          />
        </div>
        <div className="mb-3">
          <label htmlFor="rewards" className="form-label">Rewards (comma-separated)</label>
          <input
            type="text"
            className="form-control"
            id="rewards"
            value={rewards}
            onChange={(e) => setRewards(e.target.value)}
            required
          />
        </div>

        <button type="submit" className="btn btn-primary">Generate Task</button>
      </form>

      {/* Display the Generated Task */}
      {generatedTask && (
        <div className="card mt-5" style={{ padding: '20px', textAlign: 'left', maxWidth: '400px', margin: 'auto' }}>
          <h5>Generated Minecraft Task</h5>
          <p><strong>Description:</strong> {generatedTask.description}</p>
          <p><strong>Duration:</strong> {generatedTask.duration} hours</p>
          <p><strong>Rewards:</strong> {generatedTask.rewards}</p>
          <p><strong>Price:</strong> {generatedTask.price} ETH</p>
          <Link to='/transaction' className="btn btn-success" style={{ marginTop: '10px' }}>
            Minecraft World Task Created!
          </Link>
        </div>
      )}
    </div>
  );
};

export default MinecraftTaskGenerator;
