import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Style/Creator.css';

const Creator = () => {
    const [isAccepted, setIsAccepted] = useState(false);
    const [showDetails, setShowDetails] = useState(false);

    // Sample quest data
    const quest = {
        title: "Minecraft Quest: Build and Explore",
        description: "Complete this quest by building structures and exploring new areas on your Minecraft server.",
        pluginRequired: "XGaming Quest Plugin",
        rewards: "100 Tokens, Exclusive Sword Skin",
        details: "This quest is sponsored by Comets. Add the XGaming plugin to your Minecraft server to enable quest tracking and reward distribution."
    };

    // Handle accept and reject actions
    const handleAccept = () => {
        setIsAccepted(true);
    };

    const handleReject = () => {
        setIsAccepted(false);
        setShowDetails(false);
    };

    // Toggle quest details view
    const toggleDetails = () => {
        setShowDetails(!showDetails);
    };

    return (
        <div className="creator-container">
            <h1 className="creator-title">Creator Quest Dashboard</h1>

            {/* Quest Card */}
            <div className="quest-card">
                <h2 className="quest-title">{quest.title}</h2>
                <p className="quest-description">{quest.description}</p>

                <div className="button-group">
                    <button onClick={handleAccept} className="accept-btn">Accept Quest</button>
                    <Link to='/task' onClick={handleReject} className="reject-btn">Reject Quest</Link>
                    <button onClick={toggleDetails} className="details-btn">
                        {showDetails ? "Hide Details" : "View Details"}
                    </button>
                </div>
            </div>

            {/* Quest Details Modal */}
            {showDetails && (
                <div className="quest-details-modal">
                    <h3>Quest Details</h3>
                    <p><strong>Required Plugin:</strong> {quest.pluginRequired}</p>
                    <p><strong>Rewards:</strong> {quest.rewards}</p>
                    <p><strong>Details:</strong> {quest.details}</p>
                    <button onClick={toggleDetails} className="close-details-btn">Close</button>
                </div>
            )}

            {/* Acceptance Popup */}
            {isAccepted && (
                <div className="popup">
                    <div className="popup-content">
                        <h2>Quest Accepted!</h2>
                        <p>You have accepted the quest. Check your dashboard for more information.</p>
                        <button onClick={() => setIsAccepted(false)} className="close-popup-btn">Close</button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Creator;
