import React, { useState } from 'react';

const MinecraftPixelateImage = () => {
  const [originalImage, setOriginalImage] = useState(null);
  const [minecraftImage, setMinecraftImage] = useState(null);

  // Define a color palette resembling Minecraft blocks
  const minecraftColors = [
    '#7d7d7d', // Stone
    '#8c8c4f', // Dirt
    '#4a6d48', // Grass
    '#ab6d42', // Wood
    '#d2b48c', // Sand
    '#c2c2c2', // Iron
    '#6e6e6e', // Coal
    '#e1c699', // Birch
    '#007c7f', // Diamond
    '#ffd700', // Gold
  ];

  // Function to handle file upload
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        setOriginalImage(e.target.result);
        convertToMinecraftBlocks(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  // Convert image to Minecraft-like pixel blocks
  const convertToMinecraftBlocks = (imageSrc) => {
    const img = new Image();
    img.src = imageSrc;

    img.onload = () => {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const pixelationLevel = 16; // Adjust block size for Minecraft effect

      // Set canvas size to the image size
      canvas.width = img.width;
      canvas.height = img.height;

      // Draw the image in a scaled-down version to pixelate it
      ctx.drawImage(img, 0, 0, img.width / pixelationLevel, img.height / pixelationLevel);

      // Get image data and replace colors with Minecraft palette
      const imgData = ctx.getImageData(0, 0, img.width / pixelationLevel, img.height / pixelationLevel);
      const data = imgData.data;

      // Process each pixel
      for (let i = 0; i < data.length; i += 4) {
        const [r, g, b] = [data[i], data[i + 1], data[i + 2]];

        // Find closest Minecraft color
        const closestColor = getClosestMinecraftColor(r, g, b);
        data[i] = closestColor[0];
        data[i + 1] = closestColor[1];
        data[i + 2] = closestColor[2];
      }

      // Put the modified pixel data back to canvas
      ctx.putImageData(imgData, 0, 0);

      // Scale the pixelated image up to simulate Minecraft blocks
      ctx.imageSmoothingEnabled = false;
      ctx.drawImage(
        canvas,
        0,
        0,
        img.width / pixelationLevel,
        img.height / pixelationLevel,
        0,
        0,
        img.width,
        img.height
      );

      // Convert the canvas to a data URL and update state
      setMinecraftImage(canvas.toDataURL());
    };
  };

  // Helper function to find the closest Minecraft color in the palette
  const getClosestMinecraftColor = (r, g, b) => {
    let closestColor = minecraftColors[0];
    let smallestDistance = Infinity;

    minecraftColors.forEach((hex) => {
      const color = hexToRgb(hex);
      const distance = Math.sqrt(
        Math.pow(r - color[0], 2) + Math.pow(g - color[1], 2) + Math.pow(b - color[2], 2)
      );

      if (distance < smallestDistance) {
        smallestDistance = distance;
        closestColor = color;
      }
    });

    return closestColor;
  };

  // Helper function to convert hex color to RGB
  const hexToRgb = (hex) => {
    const bigint = parseInt(hex.slice(1), 16);
    return [(bigint >> 16) & 255, (bigint >> 8) & 255, bigint & 255];
  };

  return (
    <div className="container text-center" style={{ padding: '50px' }}>
      <h2>Upload Logo</h2>
      <input type="file" accept="image/*" onChange={handleFileUpload} style={{ marginBottom: '20px' }} />
      
      {originalImage && (
        <div>
          <h4>Original Image</h4>
          <img src={originalImage} alt="Original" style={{ maxWidth: '100%', height: 'auto', marginBottom: '20px' }} />
        </div>
      )}

      {minecraftImage && (
        <div>
          <h4>Minecraft Pixelated Image</h4>
          <img src={minecraftImage} alt="Minecraft Pixelated" style={{ maxWidth: '100%', height: 'auto', marginBottom: '20px' }} />
        </div>
      )}
    </div>
  );
};

export default MinecraftPixelateImage;
